.stars {
  display: flex;

  &__star {
    width: 16px;
    height: 16px;
    margin-right: 4px;
    background-repeat: no-repeat;
    background-position: center;
    background-image: url(../images/star.svg);
  }

  @for $index from 1 through 5 {
    &--#{$index} {
      .stars__star:nth-child(-n + #{$index}) {
        background-image: url(../images/star-active.svg);
      }
    }
  }
}
